import React from "react";
import emojipedia from "../emojipedia"
import Dictionary from "./Dictionary";

function callEmoji(words){
  return <Dictionary emoji ={words.emoji} name = {words.name} meaning = {words.meaning} />
}

function App() {
  return (
    <div>
      <h1>
        <span>emojipedia</span>
      </h1>

      <dl className="dictionary">
      {emojipedia.map(callEmoji)}
      </dl>
    </div>
  );
}

export default App;